import React,{Component} from "react";



class App extends Component {
  constructor(props){
    super(props)
    this.state = {
      name:'',
      list :[]
    }
  }

  addItem(){
    if(this.state.name !== ''){
      const name = {
        id : Math.random(),
        value : this.state.name
      }
      const list = [...this.state.list]
      list.push(name)
      this.setState({
        name :'',
        list,
      })
    }
  }
  deleteItem(key){
    const list = [...this.state.list]

    const updateList = list.filter(item => item.id!==key)

    this.setState({list:updateList})
  }
  handleClick(event){
    this.setState({name:event})
  }
  render(){
    return(
      <div className='list'>
        <h1 className='heading'>To Do App</h1>
        <input placeholder='add item..' className='text' type='text' value={this.state.name} onChange={item => this.handleClick(item.target.value)}></input>
        <button className='button' onClick={()=> this.addItem()}>Send</button>
        <h1 className='center'>
        {this.state.list.map(item => {return(
          <p className='update-list' onClick={() => this.deleteItem(item.id)}>{item.value}</p>
        )})}
       </h1>
      </div>
    )
  }
}

export default App;
